import logobg from "@/components/login/loginbg.vue"
export default {
	data() {
		return {
			value1:"",
			value2:"",
			bool:false
		}
	},
	methods: {
		Losefocus(){
			this.bool = true
		},
		Logsubmit(){
			let value1 = this.value1;
			let value2 = this.value2
			if(value1=="" && value2==""){
				return false
			}else{
				//进行请求后台数据接口 进行匹配密码跳转首页 将用户名和密码应用到缓存里
				uni.switchTab({
					url:"/pages/index/index"
				})
			}
		},
		smslogin(url){
			uni.navigateTo({
				url
			})
		}
	},
	components:{
		logobg
	}
}
